video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  aspect-ratio: 6/2;
  -o-object-fit: cover;
  object-fit: cover;
  -o-object-position: center;
  object-position: center;
}
.overlay {
  min-height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  position: absolute;
  width: 100%;
}

.overlay h1,
h3 {
  color: rgb(247, 233, 233);
  font-weight: 700;
  font-size: 64px;
  mix-blend-mode: overlay;
  text-align: center;
  padding: 1%;
  margin-top: 10%;
  width: auto;
}
.overlay h3 {
  text-align: center;
  margin: auto;
  width: 50%;
  font-size: 20px;
  font-weight: 500;
  padding-bottom: 40px;
}

.iconsDiv{
  /* border: 1px solid; */
  width: 20%;
  display: flex;
  justify-content: space-evenly;
  margin: auto;
  padding: auto;
  color: rgb(247, 233, 233);
  font-weight: 700;
  font-size: 25px;
}

