@media (min-width: 701px) and (max-width: 1120px) {
  video {
    aspect-ratio: 6/4;
  }
  .overlay h1 {
    font-size: 42px;
    margin-top: 25%;
  }
  .overlay h3 {
    font-size: 18px;
    width: 80%;
  }
}


@media (max-width: 700px) {
  video {
    aspect-ratio: 2/2;
  }
  .overlay h1 {
    font-size: 34px;
    margin-top: 38%;
  }
  .overlay h3 {
    font-size: 16px;
    width: 80%;
  }
  .iconsDiv{
    width: 30%;
  }
}
